.message {
    display: flex;
    align-items: flex-end;
    margin: 15px;
}

.imageAndText {
    display: flex;
    align-items: flex-end;
    margin: 15px;
}

.imageAndText img {
    border: black solid thin;
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.text {
    background-color: darkseagreen;
    padding: 10px;
    border-radius: 15px 15px 15px 0px;
}

.text:after {
}

.name {
    font-size: 18px;
    font-weight: bold;
    color: darkslategray;
}

.messageText {
    min-width: 200px;
    max-width: 1000px;
}

.time {
    display: flex;
    justify-content: flex-end;
}

.message_angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 15px, darkseagreen 0);
}